body {
  background-color: #eee;
}

::selection {
  background: #6c00ff;
  color: #ffffff;
}

::-moz-selection {
  background: #6c00ff;
  color: #ffffff;
}

.wrap {
  width: 350px;
  height: 525px;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  border-radius: 20px;
  box-shadow: 0px 20px 40px #00000066;
  font-family: Roboto, sans-serif;
  background-color: #041936;
  color: #ffffff;
}

.wrap .title {
  width: 100%;
  height: 109px;
  text-align: right;
  padding: 16px;
}

.wrap .title h3 {
  color: #00c4ff;
  font-size: 16px;
  line-height: 1em;
}

.wrap .title h2 {
  font-size: 56px;
  line-height: 1.2em;
}

.wrap .btnsWrap {
  width: 100%;
  height: 416px;
  padding: 16px 8px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  background-color: #062145;
  border-radius: 0 0 20px 20px;
}

.wrap .btn {
  width: 72px;
  height: 64px;
  line-height: 64px;
  margin-bottom: 16px;
  text-align: center;
  font-size: 28px;
  border-radius: 16px;
  cursor: pointer;
  user-select: none;
  -webkit-user-select: none;
  -moz-user-select: none;
}

.wrap .btn:hover {
  background-color: #04162e;
}

.wrap .btn:active {
  background-color: #020b16;
}

.wrap .l-btn--w2 {
  width: 159px;
  background: linear-gradient(90deg, #00c4ff, #6c00ff);
  padding: 0 1em;
  text-align: right;
}

.wrap .l-btn--w2:hover {
  background: linear-gradient(45deg, #00c4ff, #6c00ff);
}

.wrap .l-btn--w2:active {
  background: #00c4ff;
}

.l-color-pri {
  color: #00c4ff;
}

.l-bgcolor-pri {
  background-color: #041936;
}

.l-bgcolor-pri:hover {
  background-color: #000307;
}

.clearfix {
  overflow: hidden;
}

.l-fz0 {
  font-size: 0;
}
